<template>
    <v-container>
        <v-card>
            <v-card-title>Sign-in</v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-text-field 
                    label="username"
                    v-model="username"
                    @keydown="keydown"
                ></v-text-field>
                <v-text-field
                    type="password"
                    label="password"
                    v-model="password"
                    @keydown="keydown"
                ></v-text-field>
                <v-card-actions>
                    <v-btn
                        :loading="buttonLoading"
                        block
                        color="primary"
                        @click="loginUser"
                    >Iniciar Sesión</v-btn>
                </v-card-actions>
            </v-card-text>
        </v-card>
    </v-container>
</template>

<script>
import { mapMutations } from 'vuex';

export default {
    name: 'login-user',
    data(){
        return{
            username: '',
            password: '',
            buttonLoading: false
        }
    },
    methods: {
        ...mapMutations(['login']),
        keydown({key}){
            if(key === 'Enter') this.loginUser()
        },
        loginUser(){
            this.buttonLoading = true
            setTimeout(() => {
                this.login({username: this.username, password: this.password})
                if(this.username == this.$store.state.user && this.$store.state.password == this.password) this.$router.push('/')
                this.buttonLoading = false
            }, 1500);
        }
    }
}
</script>

