<template>
    <v-dialog
      v-model="dialog"
      width="500"
    >
      <v-card
          outlined
      >
          <v-card-title>Detalles del Origen</v-card-title>
          <v-card-text>
            <v-divider></v-divider>
            <v-expansion-panels>
                <v-expansion-panel 
                    v-for="(destination, index) in destinations"
                    :key="index"    
                >
                    <v-expansion-panel-header>Destino {{ index + 1 }}</v-expansion-panel-header>
                    <v-expansion-panel-content>
                        <v-icon left>home</v-icon> Dirección: {{ destination.address }} <br/>
                        <v-icon left>stairs</v-icon> Piso: {{ destination.floor }} <br />
                        <v-icon left>comment</v-icon> Mensaje Opcional: {{ destination.optionalMessage }} <br/>
                        <v-icon left>contact_mail</v-icon> Contacto: {{ destination.contact }} <br/>
                        <v-icon left>contact_phone</v-icon> Telefono celular: {{ destination.phone }} <br/>
                    </v-expansion-panel-content>
                </v-expansion-panel>
            </v-expansion-panels>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions>
              <v-btn 
                  block 
                  color="primary"
                  @click="dialog = !dialog"
              >
                  Aceptar
              </v-btn>
          </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  export default {
      name: 'OriginDetails',
      props: {
          value: {
              type: Boolean,
              required: true
          },
          destinations: {
              type: Array,
              required: true
          }
      },
      computed:{
          dialog:{
              get(){
                  return this.value
              },
              set(value){
                  this.$emit('input', value)
              }
          }
      }
  }
  </script>
  
