<template>
    <v-container class="py-8">
        <v-card :loading="loadingCard">
            <v-row >
                <v-col cols="6">
                    <v-card-title>Administración de viajes</v-card-title>
                </v-col>
            </v-row>
            <v-data-table
                :headers="headers"
                :items="items"
                :items-per-page="5"
                class="elevation-1"
            >
                <template v-slot:[`item.origin`]="{ item }">
                    <v-btn 
                        block 
                        color="primary"
                        @click="OpenOriginDetails(item.origin)"
                    >
                        <v-icon left>add</v-icon> info
                    </v-btn>
                </template>
                <template v-slot:[`item.destinations`]="{ item }">
                    <v-btn 
                        block 
                        color="primary"
                        @click="OpenDestinationsDetails(item.destinations)"
                    >
                        <v-icon left>add</v-icon> info
                    </v-btn>
                </template>
            </v-data-table>
        </v-card>
        <origin-details 
            v-model="originDetails" 
            :origin="originSelectionedDetails"
        />
        <destinations-details
            v-model="destinationsDetails"
            :destinations="desinationsSelectedDetails"
        />
    </v-container>
</template>

<script>
import provider from '@/providers'
import OriginDetails from '@/components/TravelManagement/OriginDetails.vue'
import DestinationsDetails from '@/components/TravelManagement/DestinationsDetails.vue'

export default {
    name:'UserManagement-Page',
    components: {
        OriginDetails,
        DestinationsDetails
    },
    data () {
      return {
        destinationsDetails: false,
        desinationsSelectedDetails: [],
        originDetails: false,
        originSelectionedDetails: {},
        updateUsersLoading: false,
        usersToUpdate: [],
        loadingCard: false,
        headers: [
            {
                text: 'Cliente',
                value: 'designedClient',
                align: 'center'
            },
            {
                text: 'Conductor',
                value: 'designedDriver',
                align: 'center'
            },
            {
                text: 'Origen',
                value: 'origin',
                align: 'center'
            },
            {
                text: 'Destinos',
                value: 'destinations',
                align: 'center'
            },
            {
                text: 'Metodo de pago',
                value: 'methodOfPay',
                align: 'center'
            },
            {
                text: 'Tipo de vehículo',
                value: 'vehicle',
                align: 'center'
            },
            {
                text: 'Estado',
                value: 'status',
                align: 'center'
            }
        ],
        items: [
        ],
      }
    },
    async mounted(){  
        this.loadingCard = true
        try {
            const travels = await provider.fetchTravels()
            console.log('travels: ', travels)
            this.items = travels.map(travel => travel)

        } catch (error) {
            console.log('error: ', error)
        }
        this.loadingCard = false
    },
    methods:{
        async OpenDestinationsDetails(destinations){
            this.desinationsSelectedDetails = destinations
            this.destinationsDetails = true
        },
        async OpenOriginDetails(origin){
            this.originSelectionedDetails = origin
            this.originDetails = true
        },
        async updateUsers(){
            this.updateUsersLoading = true
            for(let i = 0; i<this.usersToUpdate.length; i++){
                const {id, email, role, name, surname, address, state} = this.usersToUpdate[i]
                await provider.updateUser({id, email, role, name, surname, address, state})
            }
            this.existUser = false
            this.usersToUpdate = []
            this.updateUsersLoading = false
        },
        driverEnabledChanged({id, email, role, name, surname, address, state}){
            let existUser = false
            this.usersToUpdate.forEach(async (userToUpdate, index) => {
                if(userToUpdate.id === id){
                    this.usersToUpdate[index] = {id, email, role, name, surname, address, state}
                    existUser = true
                }
            })
            if(!existUser) this.usersToUpdate.push({id, email, role, name, surname, address, state})
        },
        editUser(){
    
        },
        deleteUser(){
    
        }
    }
}
</script>

