<template>
  <v-dialog
    v-model="dialog"
    width="500"
  >
    <v-card
        outlined
    >
        <v-card-title>Detalles del Origen</v-card-title>
        <v-card-text>
            <v-divider></v-divider>
            <v-icon left>home</v-icon> Dirección: {{ origin.address }} <br/>
            <v-icon left>stairs</v-icon> Piso: {{ origin.floor }} <br />
            <v-icon left>comment</v-icon> Mensaje Opcional: {{ origin.optionalMessage }} <br/>
            <v-icon left>contact_mail</v-icon> Contacto: {{ origin.contact }} <br/>
            <v-icon left>contact_phone</v-icon> Telefono celular: {{ origin.phone }} <br/>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
            <v-btn 
                block 
                color="primary"
                @click="dialog = !dialog"
            >
                Aceptar
            </v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
    name: 'OriginDetails',
    props: {
        value: {
            type: Boolean,
            required: true
        },
        origin: {
            type: Object,
            required: true
        }
    },
    computed:{
        dialog:{
            get(){
                return this.value
            },
            set(value){
                this.$emit('input', value)
            }
        }
    }
}
</script>

