<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
      clipped-left
    >
      <v-app-bar-nav-icon @click="menuDrawerState=true" v-if="user"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <b class="text-h4">BACKOFFICE</b>
      <v-spacer></v-spacer>
      <v-icon>person</v-icon>
    </v-app-bar>

    <v-main>
      <router-view/>
    </v-main>

    <menu-drawer v-if="user" :value="menuDrawerState" @set-drawer="setMenuDrawerState"/>
  </v-app>
</template>

<script>
import MenuDrawer from './components/AppComponents/MenuDrawer.vue';

export default {
  name: 'App',  
  components: {
    MenuDrawer
  },
  data(){
    return{
      menuDrawerState: false
    }
  },
  computed: {
    user(){
      return (this.$store.state.user == this.$store.state.root.user && this.$store.state.password == this.$store.state.root.password)
    }
  },
  created(){
    if(this.user){
      if(this.$router.currentRoute.path != '/') this.$router.push('/')
    }else {
      if(this.$router.currentRoute.path != '/login-user') this.$router.push('/login-user')
    }
  },
  methods:{
    setMenuDrawerState(value){
      this.menuDrawerState = value
    }
  }
};
</script>
